import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';


import { DistressAlertsRoutes } from './distress-alerts.routing';

import { SweetAlertComponent } from './sweetalert/sweetalert.component';
import {DistressAlertsComponent} from "./distress-alerts.component";

import { JWBootstrapSwitchModule } from 'jw-bootstrap-switch-ng2';

@NgModule({
    imports: [
        CommonModule,
        RouterModule.forChild(DistressAlertsRoutes),
        FormsModule,
        JWBootstrapSwitchModule
    ],
    declarations: [
        SweetAlertComponent,
        DistressAlertsComponent
    ]
})

export class DistressAlertsModule {}



